@font-face {
   font-family: 'Pretendard-Regular';
   src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
   font-weight: 400;
   font-style: normal;
}
@font-face {
   font-family: 'S-CoreDream-4Regular';
   src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-4Regular.woff') format('woff');
   font-weight: normal;
   font-style: normal;
}
*{
   margin: 0%;
   padding: 0%;
   box-sizing: border-box;
}
.callMenu{
   position: fixed;
   display: flex;
   justify-content: center;
   align-items: center;
   z-index: 10020;
   top: 0;
   right: 0;
   width: 30px;
   height: 30px;
   font-size: larger;
}
.TopBar{
   width: 100%;
   height: 75px;
   position: fixed;
   padding: 8px 18px;
   font-family: 'Pretendard-Regular';
   display: flex;
   flex-direction: column;
   font-weight: 700;
   z-index: 10000;
   top: 0;
   background-color: rgba(228, 225, 225, 0.39);
   transition: 0.5s ease;
}
.TopBar2{
   width: 100%;
   height: 75px;
   position: fixed;
   padding: 8px 18px;
   font-family: 'Pretendard-Regular';
   display: flex;
   flex-direction: column;
   font-weight: 700;
   transition: 0.5s ease;
   z-index: 10000;
   top: -75px;
}
.menus{   
   width: 100%;
   height: 100%;
}
.menus>ul{
   display: flex;
   width: 100%;
   height: 100%;
   list-style: none;
}
.menus>ul>li{
   width: 33.33%;
   display: flex;
   justify-content: center;
   align-items: center;
}
.menus>ul>li>a{
   text-decoration: none;
   color: black;
   font-weight: 500;
   font-family: 'S-CoreDream-4Regular';
}
.toTop{
   display: none;
}
.toTop2{
   cursor: pointer;
   position: fixed;
   display: flex;
   justify-content: center;
   align-items: center;
   bottom: 6px;
   right: 6px;
   width: 35px;
   height: 35px;
   background-color: lightgray;
   font-size: x-large;
   border-radius: 50%;
   color: white;
}
.sections{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   height: 100vh;
}
.bonmun{
   width: 100%;
   height: 95%;
   padding: 15px;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}
.btns{
   cursor: pointer;
   width: 100%;
   height: 5%;
   font-size: large;
   border: none;
   background: transparent;
   transition: 0.25s ease;
   color: rgba(128, 128, 128, 0.712);
}
.btns:hover{
   color: gray;
   background-color: rgba(194, 194, 194, 0.432);
}
.callMenu{
   cursor: pointer;
}
.unescapeHtml {
   font-family: 'S-CoreDream-4Regular';
   font-size: 1.25rem;
   margin: 0;
   text-align: center;
   letter-spacing: 3px;
   line-height: 55px;
}
.bomu{
   width: 100%;
   height: 50%;
   display: flex;
   justify-content: center;
   align-items: center;
}
.ballDeco{
   width: 100%;
   height: 50%;
   display: grid;
   grid-template-columns: repeat(2,1fr);
   grid-template-rows: repeat(2,150px);
	row-gap: 10px;
}
.ballDeco>span{
   text-align: center;
   font-size: 12pt;
   width: 140px;
   height: 140px;
   border: 0.15rem solid black;
   border-radius: 50%;
   display: flex;
   justify-content: center;
   align-items: center;
   letter-spacing: 1px;
   font-weight: 700;
	align-self: center;
	justify-self: center;
}
.moreBtn{
   display: inline-block;
   width: 100px;
   border-radius: 20px;
   height: 35px;
   border: 1px solid rgb(51, 190, 236);
   margin-top: 20px;
   text-align: center;
   line-height: 32px;
   text-decoration: none;
   color: rgb(51, 190, 236);
}
.moreBtn:hover{
   background-color: rgb(51, 190, 236);
   color: white;
}
.qnaSect{
   width: 100%;
   height: 100vh;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}
.cpt{
   width: 100%;
   height: 65%;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   padding: 17px;
}
.oneBtns{
   width: 100%;
   height: 25%;
   display: flex;
   justify-content: space-evenly;
   align-items: center;
}
.oneBtns>span{
   border: 1px solid rgb(51, 190, 236);
   border-radius: 50%;
   width: 55px;
   height: 55px;
   text-align: center;
   line-height: 55px;
   font-size: x-large;
   color: rgb(51, 190, 236);
}
.oneBtns>span>a{
   display: inline-block;
   width: 100%;
   border-radius: 50%;
   height: 100%;
   text-decoration: none;
   color: rgb(51, 190, 236);
}
.oneBtns>span:hover{
   background-color: rgb(51, 190, 236);
   color: white;
   transition: 0.3s ease;
   cursor: pointer;
}
.oneBtns>span>a:hover{
   color: white;
}
.unescapeHtml2 {
   font-family: 'S-CoreDream-4Regular';
   width: 100%;
   font-size: 1.5rem;
   font-weight: 700;
   text-align: center;
   letter-spacing: 3px;
   line-height: 60px;
}